import React from 'react';
import './static-mobile.scss';
import './static-pc.scss';
import emoji from '../../assets/images/emoji_illustration.png';

const StaticSection = () => (
    <section id="static_section" className="static_section">
        <div className="static_section--block_0 static_section--block">
            <h1>
                МИР УМНЫХ ТЕХНОЛОГИЙ&nbsp;
                <span>
                    В вашем <br/>
                    автомобиле
                </span>
            </h1>
            <p>
                Полезные сервисы и развлечения <br/> 
                в едином интерфейсе
            </p>
        </div>
        <div className="static_section--block_1 static_section--block">
            <h2>Преимущества</h2>
            <p>
                Безопасность и комфорт в управлении <br/>
                системами авто во время вождения
            </p>
            <img src={emoji} alt=""/>
        </div>
        <div className="static_section--block_2 static_section--block">
            <h1>
                <span>
                    СВЯЖИТЕСЬ <br/>
                    С НАМИ
                </span>
            </h1>
            <p>
                По всем вопросам обращайтесь на:
                <a href="mailto: offers@Stopol-auto.com">offers@Stopol-auto.com</a>
            </p>
        </div>

        <span className="static_section--copyright">Stopol Auto 2020 все права защищены</span>
    </section>
);

export default StaticSection;